html, body {
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: sans-serif; }

#container {
  height: 100%;
  background-color: #f0f2f4; }

.main {
  display: -ms-flexbox;
  display: flex;
  text-align: center; }
  .main > svg {
    padding: 30px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .main > div {
    padding: 10px 50px; }

.modal {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.52); }
  .modal > div {
    margin-top: -20%; }

.dialog {
  padding: 20px;
  background: white;
  width: 260px;
  text-align: left; }
  .dialog > h2 {
    padding: 5px;
    margin: 0;
    margin-bottom: 10px;
    font-size: 1.2em; }

.form > div {
  display: -ms-flexbox;
