  display: flex;
  background: #ebeae8;
  margin: -20px;
  margin-top: 18px;
  padding: 15px 20px;
  text-align: right; }
  .footer .spacer {
    -ms-flex: 1 1;
        flex: 1 1; }
  .footer button {
    margin: 0 5px;
    padding: 5px; }

.task .main {
  cursor: pointer;
  fill: rgba(255, 255, 255, 0.7);
  stroke: rgba(0, 0, 0, 0.6);
  r: 12;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s; }

.task .tooltip {
  fill: rgba(255, 255, 255, 0.6); }

.task .tooltip-text {
  font-size: 12px;
  text-anchor: middle; }

.task:hover .main {
